<template>
  <div>
    <vSearch
      @getOption="getOption"
      @searchInfo="searchInfo"
      @clearSearchInfo="clearSearchInfo"
      :pageType="pageType"
    ></vSearch>
    <el-table
      :data="tableData"
      style="width: 100%"
      max-height="675"
      @sort-change="sortChange"
    >
      <el-table-column
        label="操作"
        width="60"
        v-if="showOperate"
      >
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="openDialog(scope.row, 'operate')"
            type="text"
            size="small"
            :disabled="(scope.row.totalLoan/(parseInt(scope.row.goodsPrice) * parseInt(scope.row.amount) + parseInt(scope.row.deposit)))<parseInt(scope.row.recoveryRate)/100"
          >
            处理
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop="customerName"
        label="客户名称"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="customerNo"
        label="客户号"
        width="160"
      >
      </el-table-column>
      <el-table-column
        prop="contractNo"
        label="合同编号"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="goodsPrice"
        label="当前价格"
        sortable
      >
        <template slot-scope="scope">
          {{tofixed(scope.row.goodsPrice)}}
        </template>
      </el-table-column>
      <el-table-column
        prop="warningPrice"
        label="预警价格"
        sortable
      >
        <template slot-scope="scope">
          {{tofixed(scope.row.warningPrice)}}
        </template>
      </el-table-column>
      <el-table-column
        prop="pledgeRate"
        label="当前担保率"
        :formatter="pledgeRateFormatter"
      >
      </el-table-column>
      <el-table-column
        prop="contactMember"
        label="联系客户"
        :formatter="contactFormatter"
        sortable
      >
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="prev, pager, next, jumper"
        :total="totalPage"
      >
      </el-pagination>
    </div>

    <el-dialog
      title="处理"
      :visible.sync="dialogFormVisible"
    >
      <el-tabs type="border-card">
        <el-tab-pane label="价值预警">
          <table class="value-warning">
            <tr>
              <th>放款金额</th>
              <td>{{this.tofixed(warningForm.totalLoan)}}</td>
              <!-- <th>&nbsp;</th>
              <td>&nbsp;</td> -->
            </tr>
            <tr>
              <th>仓单原价值</th>
              <td>{{this.tofixed(warningForm.originalPrice)}}</td>
              <th>仓单现价值</th>
              <td>{{this.tofixed(warningForm.amount * warningForm.goodsPrice)}}</td>
            </tr>
            <tr>
              <th>原保证金</th>
              <td>{{this.tofixed(warningForm.deposit)}}</td>
              <th>现保证金</th>
              <td>{{this.tofixed(warningForm.realDeposit)}}</td>
            </tr>
          </table>
        </el-tab-pane>
        <el-tab-pane label="保值方案">
          <div class="program">
            需提醒客户追加{{keepValuePlan.x>0 && isFinite(keepValuePlan.x)?tofixed(keepValuePlan.x):"-"}}吨的货物或{{keepValuePlan.y>0?tofixed(keepValuePlan.y):"-"}}元保证金。
          </div>
        </el-tab-pane>
        <el-tab-pane label="风险处理">
          <div class="program">
            客户实际追加
            <el-input
              v-model="programForm.weight"
              clearable
              @focus="addWarehouse"
              style="width:120px"
            >
            </el-input>
            吨的货物，
            <el-input
              v-model="programForm.realDeposit"
              clearable
              style="width:120px"
            >
            </el-input>
            元保证金。
          </div>
          <el-button
            type="primary"
            class="dialog-button"
            @click="submitForm"
          >确定</el-button>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
    <el-dialog
      title="追加仓单："
      :visible.sync="warehouseInnerVisible"
      width="1200px"
      :close-on-click-modal="false"
      append-to-body
    >
      <div class="tableDate">
        <div
          class="button"
          style="width:3%;float:right;"
        >
          <P>
            <el-button
              class="el-icon-plus"
              @click.prevent="addRow()"
            ></el-button>
          </P>
          <p>
            <el-button
              class="el-icon-minus"
              @click.prevent="delData()"
            ></el-button>
          </p>
        </div>
        <div class="table">
          <el-table
            :data="addWareHouseTable"
            ref="addWareHouseTable"
            tooltip-effect="dark"
            border
            stripe
            style="width: 95%"
            @selection-change="selectRow"
          >
            <el-table-column
              type="selection"
              width="45"
              align="center"
            ></el-table-column>
            <el-table-column
              label="仓单编号"
              align="center"
              width="195"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.wwNo"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="商品名称"
              width="90"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.goodsName"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="品牌/牌号"
              width="100"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.brand"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="质量标准"
              width="110"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.qualityStandard"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="包装规格"
              width="115"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.packingSpecification"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="数量"
              width="105"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.quantity"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="计量单位"
              width="80"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.unit"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="仓库名称"
              width="115"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.warehouseName"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="仓库地址"
              width="205"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.warehouseAddress"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="联系人"
              width="115"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.linkman"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="联系电话"
              width="165"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.linkPhone"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="货物位置"
              width="165"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.goodsAddress"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="货物真实性"
              width="115"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.goodsAuthenticity"></el-input>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          @click="addWareHouseTableToProgram"
        >确 定</el-button>
        <!-- <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button> -->
      </div>
    </el-dialog>
  </div>
</template>

<script>
import vSearch from "../components/Search.vue";
import * as ajaxUrl from "../ajax.config.js";

export default {
  data() {
    return {
      pageType: 2,
      searchForm: {
        option: "",
        inputText: ""
      },
      searchParam: {},
      warningForm: {},
      keepValuePlan: {},
      programForm: {
        weight: "",
        realDeposit: ""
      },
      currentPage: 1,
      pageSize: 10,
      totalPage: 1,

      dialogFormVisible: false,
      methodVisible: false,
      warehouseInnerVisible: false,

      selectlistRow: [],
      addWareHouseTable: [], //追加仓单信息
      wareHouseTable: [],
      tableData: [],
      showOperate: true
    };
  },
  mounted() {
    this.searchInfo();
  },
  methods: {
    getOption(value, inputText) {
      this.searchForm.option = value;
      this.searchForm.inputText = inputText;
    },
    searchInfo(orderField,orderType) {
      this.searchParam[this.searchForm.option] = this.searchForm.inputText;
      this.axios
        .get(ajaxUrl.GETWARNINGINFOLIST, {
          params: {
            pageSize: this.pageSize,
            pageNum: this.currentPage,
            search: this.searchParam,
            status: 0,
            orderField: orderField,
            orderType: orderType
          }
        })
        .then(res => {
          if(res.data.resCode == 200) {
            this.tableData = res.data.resData;
            this.totalPage = res.data.page.total;
            this.pageSize = res.data.page.pageSize;
            this.currentPage = res.data.page.current;
            this.searchParam = {};
            sessionStorage.setItem("nowTime", res.data.nowdate);
            this.isTxTime()
          }else{
            this.$message.error(res.data.resMsg);
          }
          
        })
        .catch(err => {
          console.log(err);
        });
    },
    clearSearchInfo() {
      this.searchForm.option = "";
      this.searchForm.inputText = "";
    },
    openDialog(row, type) {
      if (type == "operate") {
        this.dialogFormVisible = true;
        this.addOperateStatus(row.id, 1);
        this.warningForm = row;
        this.programForm.contractId = row.contractId;
        this.programForm.warningPrice = row.warningPrice;
        this.programForm.totalPrice = row.totalPrice;
        this.programForm.contractDeposit = row.deposit;
        this.programForm.id = row.id;
        this.keepValuePlan.x = row.originalPrice / row.goodsPrice - row.amount;
        this.keepValuePlan.y = row.originalPrice - row.amount * row.goodsPrice;
      } else if (type == "method") {
        this.methodVisible = true;
      }
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.searchInfo();
    },
    formatter(row, column) {
      return row.pledgeRate + "%";
    },
    pledgeRateFormatter(row) {
      let num = row.totalLoan/(parseInt(row.goodsPrice) * parseInt(row.amount) + parseInt(row.deposit));
      return ((num*100).toFixed(2)+"%")
    },
    contactFormatter(row, column) {
      return row.customerLinkman + " " + row.customerPhone;
    },
    addWarehouse() {
        this.warehouseInnerVisible = true;
    },
    // 获取表格选中时的数据
    selectRow(val) {
      this.selectlistRow = val;
    },
    // 增加行
    addRow() {
      let list = {
        rowNum: this.addWareHouseTable.length + 1,
        wwNo: this.wwNo,
        goodsName: this.goodsName,
        brand: this.brand,
        qualityStandard: this.qualityStandard,
        packingSpecification: this.packingSpecification,
        quantity: this.quantity,
        unit: this.unit,
        warehouseName: this.warehouseName,
        warehouseAddress: this.warehouseAddress,
        linkman: this.linkman,
        linkPhone: this.linkPhone,
        goodsAddress: this.goodsAddress,
        goodsAuthenticity: this.goodsAuthenticity,
      };
      this.addWareHouseTable.unshift(list);
    },
    // 删除选中行
    delData() {
      for (let i = 0; i < this.selectlistRow.length; i++) {
        let val = this.selectlistRow;
        // 获取选中行的索引的方法
        // 遍历表格中tableData数据和选中的val数据，比较它们的rowNum,相等则输出选中行的索引
        // rowNum的作用主要是为了让每一行有一个唯一的数据，方便比较，可以根据个人的开发需求从后台传入特定的数据
        val.forEach((val, index) => {
          this.addWareHouseTable.forEach((v, i) => {
            if (val.rowNum === v.rowNum) {
              // i 为选中的索引
              this.addWareHouseTable.splice(i, 1);
            }
          });
        });
      }
      // 删除完数据之后清除勾选框
      this.$refs.addWareHouseTable.clearSelection();
    },
    addWareHouseTableToProgram(){
      let list = this.addWareHouseTable;
      let weight = 0;
      for(let i = 0; i < list.length; i++){
        if(parseInt(list[i].quantity)){
          weight = weight + parseInt(list[i].quantity);
        }
      }
      this.programForm.weight = weight;
      this.programForm.warehouseWarrantList = this.addWareHouseTable;
      this.warehouseInnerVisible = false;
    },
    submitForm(){
      this.axios.post(ajaxUrl.WARNINGINFODEAL, this.programForm)
      .then(res => {
        if(res.data.resCode == 200){
          this.dialogFormVisible = false;
          this.searchInfo();
          this.$message({
            message: res.data.resMsg,
            type: 'success'
          })
        }else{
          this.$message.error(res.data.resMsg)
        }
      }).catch(err => {
        console.log(err);
      })
    },
    //排序
    sortChange(params) {
      if(params.order == 'ascending'){
        this.searchInfo(params.prop, 'asc')
      }else if(params.order == 'descending'){
        this.searchInfo(params.prop, 'desc')
      }else{
        this.searchInfo()
      }
    },
    addOperateStatus(formId, formType){
      let params = {
        formId: formId,
        formType: formType
      };
      this.axios.post(ajaxUrl.INDEXINFO, params)
      .then(res => {
        if(res.data.resCode == 200){
          console.log("success")
        }else{
          this.$message.error(res.data.resMsg)
        }
      }).catch(err => {
        console.log(err);
      })
    },
    //
    tofixed(number) {
      return this.common.toFixed(number);
    },
    //是否在盘中
    isTxTime(){
      let time = parseInt(sessionStorage.getItem("nowTime"));
      let date = new Date(time);
      var h = date.getHours();
      var mm = date.getMinutes();
      var s = date.getSeconds();
      if(h > 9 && (h <= 11 && mm <= 30)){
        this.showOperate = false;
      }
      if((h >= 13 && mm >= 30) && h < 15){
        this.showOperate = false;
      }
    }
  },
  components: {
    vSearch
  }
};
</script>

<style>
.pagination {
  text-align: center;
}
.value-warning {
  border-spacing: 0;
  border-collapse: collapse;
}
.value-warning tr {
}
.value-warning tr th {
  width: 150px;
  height: 70px;
  border: 1px solid gray;
}
.value-warning tr td {
  width: 200px;
  height: 70px;
  text-align: center;
  border: 1px solid gray;
}
.program {
  height: 100px;
}
.dialog-button {
  float: right;
}
</style>



